import { Component, forwardRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-progress-bar',
  templateUrl: './swire-progress-bar.component.html',
  styleUrls: ['./swire-progress-bar.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwireProgressBarComponent),
      multi: true
    }
  ]
})
export class SwireProgressBarComponent implements OnInit, ControlValueAccessor {
  @Input() swireDynamicStepLabel: string = '';
  @Input() swireDynamicStepValue: string = '';
  @Input() swireDynamicOptionalKey: string = '';
  @Input() swireDynamicProgressBarData: any = [];
  @Input() showHideStepLabel:boolean = true;
  onChange: any;
  onTouch: any;
  disabled: boolean = false;
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  writeValue(val: any) {
  }
  onClick(name: string, index:any) {
    if(index !== 0){
      this.onChange(name);
    }
    
  }
  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  constructor() { }


  ngOnInit(): void {
  }

}
