import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-tab-toggle',
  templateUrl: './swire-tab-toggle.component.html',
  styleUrls: ['./swire-tab-toggle.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireTabToggleComponent
    }
  ]
})
export class SwireTabToggleComponent implements OnInit,ControlValueAccessor {

  @Input() swireDynamicClassName: string = '';
  @Input() items: any = [];
  @Input() Icon: boolean = false;
  szValue: any = null;
  disabled:boolean=false
  onChange = (szValue: any) => { };
  onTouched = () => { };

  constructor() { }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }


  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  ngOnInit(): void {
  }
  toggleButtonClick(type: boolean) {
    this.szValue = type;
    this.onChange(this.szValue);
  }

}
