import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { SwireFilterSelectboxComponent } from '../swire-filter-selectbox/swire-filter-selectbox.component';

@Component({
  selector: 'swire-input-filter',
  templateUrl: './swire-input-filter.component.html',
  styleUrls: ['./swire-input-filter.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireInputFilterComponent
    }
  ],
  host: {
    "(document:click)": "onClick($event)",
  },
})
export class SwireInputFilterComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myInput') myInput:any;
  @Input() filterData: any = [];
  @Input() atleastOneSelected: boolean = false;
  @Input() btnActiveClassName: string = '';
  @Input() btnClassName: string = '';
  @Input() bindLabel: string = '';
  @Input() bindValue: any = null;
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicReferenceName:string = '';
  @Input() swireDynamicId:string = '';
  @Input() searchBoxPlaceHolder:string = '';
  public filterdata: any = null;
  allCheckedItems: any = [];
  showList: boolean = false;
  disabled: boolean = false;
  szValue: any = null;
  touched: any = null;

  @ViewChild('inputDropdownFilter')
  inputDropdownFilter!: ElementRef;
  @ViewChild('listDropdownFilter')
  listDropdownFilter!: ElementRef;

  constructor(private _eref: ElementRef) { }

  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)){
      this.showList = false;      
    }
    this.filterdata = this.szValue;
  }
  ngOnInit(): void {
  }


  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
    this.filterdata = this.szValue;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onSelected(item: any) {
    this.markAsTouched();
    this.filterData = this.filterData.map((elm: any) => {
      if (elm[this.bindValue] == item[this.bindValue]) {
        elm.checked = true;
        this.szValue = elm[this.bindValue];
      } else {
        elm.checked = false;
      }
      return elm;
    });
    this.onChange(this.szValue);
    this.showList = false;

  }
  showHideItems() {
    this.showList = !(this.showList);
  }




}
