import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-three-way-toggle-button',
  templateUrl: './swire-three-way-toggle-button.component.html',
  styleUrls: ['./swire-three-way-toggle-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireThreeWayToggleButtonComponent
    }
  ]
})
export class SwireThreeWayToggleButtonComponent implements OnInit, ControlValueAccessor {
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicDisableFirst:boolean = false;
  @Input() swireDynamicDisableSec:boolean = false;

  szValue: any = null;
  disabled:boolean=false
  onChange = (szValue: any) => { };
  onTouched = () => { };

  constructor() { }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }


  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  ngOnInit(): void {
  }
  toggleButtonClick(type: boolean) {
    this.szValue = type;
    this.onChange(this.szValue);
  }
}
