import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
// import { CUSTOMVALIDATEMETHOD } from 'src/app/helpers/custom-validate';

@Component({
  selector: 'swire-incr-decr-input',
  templateUrl: './swire-incr-decr-input.component.html',
  styleUrls: ['./swire-incr-decr-input.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireIncrDecrInputComponent
    }
  ]
})
export class SwireIncrDecrInputComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myInput') myInput: any;
  @Input() minValue: any;
  @Input() maxValue: any;
  @Input() swireDynamicClassName:string = '';
  @Input() disabledMinValue:boolean= false;
  @Input() disabledMaxValue:boolean= false;
  szValue: any;
  touched: boolean = false;
  disabled: boolean = false;
  // CUSTOMVALIDATEMETHOD = CUSTOMVALIDATEMETHOD;

  onChange = (szValue: any) => { };

  onTouched = () => { };

  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  writeValue(szValue: number) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
  onKeyDown(e: any) {

  }
  onKeyPress(e: any) {

  }

  constructor() { }

  ngOnInit(): void {
  }
  increment(change: any) {
    this.minValue = this.szValue ? parseInt(this.szValue) : 0;
    if (change === 'incre') {
      if (!this.minValue) {
        this.minValue = 1;
      } else if (this.minValue < this.maxValue) {
        this.minValue = this.minValue + 1;
      }
      if (this.minValue <= this.maxValue) {
        this.szValue = this.minValue;
        this.onChange(this.szValue);
      }
    } else {
      if (!this.minValue) {
        this.minValue = 0;
      } else if (this.minValue > 1 && this.minValue <= this.maxValue) {
        this.minValue = this.minValue - 1
      }
      if (this.minValue > 0) {
        this.szValue = this.minValue;
        this.onChange(this.szValue);
      }
    }
  }

}
