import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NgbDateStruct, NgbCalendar, NgbDate, NgbDatepickerI18n } from '@ng-bootstrap/ng-bootstrap';
// import {MyDatepickerI18n} from '../moment'
@Component({
  selector: 'swire-datepicker',
  host: {
    '(document:mousedown)': 'onClick($event)',
  },
  templateUrl: './swire-datepicker.component.html',
  styleUrls: ['./swire-datepicker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: SwireDatepickerComponent,
      multi: true
    }
  ]
})
export class SwireDatepickerComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myDatePicker') myDatePicker: any;
  @Input() swireDynamicDatePlaceholder: string = '';
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicIconClass: string = '';
  @Input() swireDynamicColorClass: string = '';
  @Input() swireDynamicSizeClass: string = '';
  @Input() label: string = '';
  @Input() swireDynamicMinDate: any;
  @Input() swireDynamicMaxDate: any;
  @Input() swireDynamicStartDate: any;
  @Input() displayMonths: any;
  @Input() firstDayOfWeek: any

  private value: any;
  @Input() disabled: boolean = false;

  onChange: any = () => { };
  onTouch: any = () => { };

  hoveredDate: NgbDate | null = null;

  fromDate!: NgbDate;
  toDate: NgbDate | null = null;

  @Input() model!: NgbDateStruct;
  date!: { year: number; month: number; };
  constructor(private calendar: NgbCalendar, public i18n: NgbDatepickerI18n, private _eref: ElementRef) {
    // console.log(this.MyDatepickerI18n.getWeekdayLabel(2))
  }
  writeValue(obj: any): void {
    if (obj) {
      this.model = obj;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
  dateValueChanged(): void {
    this.onChange(this.value)
    this.showHideCal = false
  }
  ngOnInit(): void {
    console.log(this.calendar)

  }
  selectToday() {
    this.model = this.calendar.getToday();
  }
  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
    this.onChange(this.model)
    this.showHideCal = false
  }

  isHovered(date: NgbDate) {
    return (
      this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate)
    );
  }

  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }
  isRange(date: NgbDate) {
    return (
      date.equals(this.fromDate) ||
      (this.toDate && date.equals(this.toDate)) ||
      this.isInside(date) ||
      this.isHovered(date)
    );
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.onChange(this.model)
      this.showHideCal = false
    }
  }
  onTouched = () => { };
  touched = false;
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }
  @Input() showHideCal: any = false
  showHide() {
    if (!this.disabled) {
      this.showHideCal = !this.showHideCal
    }
  }
  nullVal: any = null;
  callFromParent() {
    this.showHideCal = false
    this.model = this.nullVal
    this.toDate = this.nullVal;
    this.fromDate = this.nullVal;
  }
  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      this.showHideCal = false;
    }
  }
}
