import { Component, EventEmitter, forwardRef, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-pagination',
  templateUrl: './swire-pagination.component.html',
  styleUrls: ['./swire-pagination.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwirePaginationComponent),
      multi: true
    }
  ]
})
export class SwirePaginationComponent implements OnInit, ControlValueAccessor {
  page = 1;
  FILTER_PAG_REGEX = /[^0-9]/g;
  showCustomizedPages: boolean = false;
  @Input() collectionSize: number = 120;
  @Input() maxSize: number = 5;
  @Input() rotateSwirePaginationLink: boolean = true;
  @Input() showEllipses: boolean = true;
  @Input() showBoundaryLinks: boolean = true;
  @Input() swirePageSize: number = 10;
  premiumData: any;
  displayedRangeOfRecords: any;
  paginateData: any[] = [];
  @Input() showPaginatorForPage: boolean = false;
  @Input() showPaginatorForItem: boolean = true;
  @Input() swireDataForPagination: any = [];
  @Input() szPaginationType: string = '';
  @Input() swiResponsiveClass: string = '';
  @Output() updateRecord: EventEmitter<any> = new EventEmitter();


  szValue = null;
  touched = false;

  disabled = false;

  onChange = (szValue: any) => { };

  onTouched = () => { };


  constructor() { }
  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  setDisabledState?(isDisabled: boolean): void {

  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e;
      this.onChange(this.szValue);
    }
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  ngOnInit(): void {
    this.premiumData = this.swireDataForPagination;
    this.collectionSize = this.premiumData?.length;
    this.getPremiumData();
  }

  selectPage(page: string) {
    this.page = parseInt(page, 10) || 1;
  }

  formatInput(input: HTMLInputElement) {
    input.value = input.value.replace(this.FILTER_PAG_REGEX, '');
  }
  getPremiumData() {
    this.paginateData = this.premiumData
      .slice((this.page - 1) * this.swirePageSize, (this.page - 1) * this.swirePageSize + this.swirePageSize);
    this.onChangeHandler(this.paginateData);

    this.displayedRangeOfRecords = `${this.swirePageSize * (this.page - 1) + 1}-${this.swirePageSize * this.page > this.collectionSize ? this.collectionSize : this.swirePageSize * this.page}`;
    this.updateRecord.emit(this.paginateData);
  }

  itemsPerPage(item: any) {
    this.swirePageSize = parseInt(item, 10) || 1;
  }



}
