import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'swire-tool-tip',
  templateUrl: './swire-tool-tip.component.html',
  styleUrls: ['./swire-tool-tip.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireToolTipComponent
    }
  ]
})
export class SwireToolTipComponent implements OnInit, ControlValueAccessor {
  @Input() SvgContent:any = '';
  @Input() swireDynamicIconWidth:string = '19px';
  @Input() swireDynamicIconHeight:string = '19px';
  @Input() swireTooltipDirectionClass:string ='';
  onChange: any;
  onTouch: any;
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  writeValue(val: any) {
    this.showHideToolTip = val;
  }

  @Input() tooltipText!: string;
  @Input() showHideToolTip: boolean = false;

  constructor() { }

  ngOnInit(): void {
  }

  showHideTooltip(val: boolean) {
    this.showHideToolTip = val;
  }
}
