import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'swire-checkbox',
  templateUrl: './swire-checkbox.component.html',
  styleUrls: ['./swire-checkbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireCheckboxComponent
    }
  ]
})
export class SwireCheckboxComponent implements OnInit, ControlValueAccessor {
  Disabled: any;
  szValue: boolean = false;
  touched: boolean = false;
  checked: boolean = false;
  @Input() isDisabled: boolean = false;
  @Input() ischecked: boolean = false;
  @Input() name: string = '';
  @Input() indeterminateCheckBoxLabel: string = "";
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicIdName:string = '';
  onChange = (szValue: any) => { };
  onTouch: any = () => { };
  writeValue(checked: boolean) {
    this.checked = checked;
  }
  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }
  registerOnTouched(onTouched: any) {
    this.onTouch = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouch();
      this.touched = true;
    }
  }
  setDisabledState(isDisabled: boolean) {
    this.isDisabled = isDisabled;

  }
  onModelChange(e: boolean) {
    // Step 5a: bind the changes to the local value
    this.checked = e;
    // Step 5b: Handle what should happen on the outside, if something changes on the inside
    this.onChange(e);
  }


  constructor() { }


  ngOnInit(): void {
  }
}
