import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'swireFilter'
})
export class SwireFilterPipe implements PipeTransform {
  transform(value: any[], filterdata: any): any[] {
    if (!value) { return []; }
    if (!filterdata) { return value; }
    filterdata = filterdata.toString().toLowerCase();
    return value.filter((it) => {
      return JSON.stringify(it).toLowerCase().includes(filterdata);
    });
  }

}
