import { Component, Input, OnInit, ViewEncapsulation, ViewChild, EventEmitter, Output, ElementRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NgbDateStruct, NgbCalendar, NgbDate,NgbDatepickerI18n } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'swire-timepicker',
  host: {
    '(document:mousedown)': 'onClick($event)',
  },
  templateUrl: './swire-timepicker.component.html',
  styleUrls: ['./swire-timepicker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: SwireTimepickerComponent,
      multi: true
    }
  ]
})
export class SwireTimepickerComponent implements OnInit, ControlValueAccessor {
  @Input() swireDynamicHours: any;
  @Input() swireDynamicMinutes: any;
  @ViewChild('myDatePicker') myDatePicker: any;
  @ViewChild('myCalender') myCalender: any;
  @Input() disabled: boolean = false;
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicIconClass: string = '';
  @Input() swireDynamicColorClass: string = '';
  @Input() swireDynamicSizeClass: string = '';
  @Input() label: string = '';
  @Input() swireDynamicMinDate: any;
  @Input() swireDynamicMaxDate: any;
  @Input() swireDynamicStartDate: any;
  @Input() displayMonths: any;
  model!: NgbDateStruct;
  date!: { year: number; month: number; };
  @Input() firstDayOfWeek:any
  time: any
  onTouch: any = () => { };
  touched = false;
  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(obj: any): void {
    if (obj) {
      this.time = obj;
    }
  } 
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    console.log('fn2', fn)
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.onChange({ time: this.time, date: this.model })
    }
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
  dateValueChanged(): void {
    this.onChange({time: this.time, date: this.model });
  }

 
  constructor(private calendar: NgbCalendar,public i18n: NgbDatepickerI18n,private _eref: ElementRef) {
    let d = new Date()
    this.model = { year: d.getFullYear(), month: d.getMonth() + 1, day: d.getDate() }
  }

  ngOnInit(): void {
    let date = new Date()
    this.time = { hour: date.getHours(), minute: date.getMinutes(),second: 0 }
  }
  onInput(): void {
    this.onChange({ time: this.time, date: this.model });
  }
  cancel() {
    console.log({ time: this.time, date: this.model })
  }
  submit() {
    this.onChange({ time: this.time, date: this.model })
  }
  onDateSelection(date: NgbDate) {
      this.model = date;
      this.onChange({ time: this.time, date: this.model })
  }
  callfunction(event:any){
    let result = document.getElementsByClassName("ngb-tp-input");
    console.log(event.path[1])
    // result[0].classList.add('active');
    // result[1].classList.add('active');
    if(event.path[1].classList.contains('ngb-tp-minute')){
      result[0].classList.remove('active');
      result[1].classList.add('active');
    }
    else{
      result[1].classList.remove('active');
      result[0].classList.add('active');
    }
  }
  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      let result = document.getElementsByClassName("ngb-tp-input");
      result[0].classList.remove('active');
      result[1].classList.remove('active');
    }
  }
}
