import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-two-way-toggle-button',
  templateUrl: './swire-two-way-toggle-button.component.html',
  styleUrls: ['./swire-two-way-toggle-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireTwoWayToggleButtonComponent
    }
  ]
})
export class SwireTwoWayToggleButtonComponent implements OnInit, ControlValueAccessor {
  @Input() dynamicDisableFirstOption:boolean = false;
  @Input() dynamicDisableSecOption:boolean = false;  
  @Input() toggleType:any;
  @Input() swireDynamicClassName:string='';
  disabled:boolean = false;
  szValue:any = null;
  touched:boolean = false;
  onChange = (szValue:any) => { };
  onTouched = () => { };

  constructor() { }

  writeValue(szValue:any) {
    // if (szValue) {
      this.szValue = szValue;
    // }

  }
  onChangeHandler(e:any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.checked;
      this.onChange(this.szValue);
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  onModelChange(e: any) {
    this.szValue = e;
    this.onChange(e);
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  ngOnInit(): void {
  }

}
