import { Component, Directive, OnInit, EventEmitter, ElementRef, ViewChild, ViewEncapsulation, Input, Output, ViewChildren, QueryList } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { KeyValue } from '@angular/common';

@Component({
  selector: 'swire-table-dynamic',
  templateUrl: './swire-table-dynamic.component.html',
  styleUrls: ['./swire-table-dynamic.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SwireDynamicTableComponent implements OnInit {
  constructor(
    private _fb: FormBuilder,
    private _el: ElementRef,
  ) { }
  objectKeys = Object.keys;
  @Input() pagination: any = false
  @Input() tableData: any
  @Input() tableDataHeading: any
  @Input() bindLabel: string = '';
  @Input() disabled: boolean = false;
  @Output() sendTableValue: EventEmitter<any> = new EventEmitter();
  @Input() tableType: any

  szValue: any = null;
  touched: any = null;
  allCheckedItems: any = [];
  showIndeterminateState: boolean = false;
  public filterdata: any = null;
  public filterDataCheckbox: any = null;
  direction: any = false
  globalIndexHead: any = 0
  ngOnInit(): void {

  }
  paginationUpdate(data: any) {
    console.log('pagination calling', data)
    this.tableData = data
  }
  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e?.name;
      if (this.allCheckedItems.includes(e)) {
        const index = this.allCheckedItems.findIndex((elm: any) => elm == e);
        if (index >= 0) {
          this.allCheckedItems.splice(index, 1);
        }
      } else {
        this.allCheckedItems.push(e);
      }
      if (this.allCheckedItems?.length > 0) {
        this.showIndeterminateState = true;
      } else {
        this.showIndeterminateState = false;
      }
      this.szValue = this.allCheckedItems;
      this.onChange(this.allCheckedItems);
      this.sendTableValue.emit(this.allCheckedItems)
    }
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }
  selectAll(e: any) {
    if (e.target.checked && !this.showIndeterminateState) {
      this.allCheckedItems.length = 0;
      this.tableData.map((elm: any) => this.allCheckedItems.push(elm));
    } else {
      this.allCheckedItems.length = 0;
      e.target.checked = false
    }
    this.showIndeterminateState = false;
    this.szValue = this.allCheckedItems;
    this.onChange(this.allCheckedItems);
    this.sendTableValue.emit(this.allCheckedItems)
  }
  checkExist(val: any) {
    if (this.szValue?.includes(val)) {
      return true;
    } else {
      return false;
    }
  }
  checkAllExist() {
    if (this.szValue?.length == this.tableData?.length) {
      this.showIndeterminateState = false;
      this.filterdata = this.filterDataCheckbox;
      return true;
    }
    return false;
  }

  onSort(event: any, val: any) {
    if (this.tableType == 'sort') {
      this.globalIndexHead = val
      this.direction = !this.direction
      let nameToSort = this.tableData[val]
      let keyToSort = Object.keys(nameToSort)[val];
      var tableData

      if (this.direction) {
        tableData = this.tableData.sort((a: any, b: any) => {
          if (a[keyToSort] > b[keyToSort]) return -1;
          if (a[keyToSort] < b[keyToSort]) return 1;
          return 0;
        });
      }
      else {
        tableData = this.tableData.sort((a: any, b: any) => {
          if (b[keyToSort] > a[keyToSort]) return -1;
          if (b[keyToSort] < a[keyToSort]) return 1;
          return 0;
        });
      }
      this.tableData = tableData
      console.log("sort calling", this.tableData, this.direction)
    }
  }
  expandableData:any = [{ name: "Sample Text", show: false }, { name: "Sample Text", show: false }, { name: "Sample Text", show: false }, { name: "Sample Text", show: false }, { name: "Sample Text", show: false }]
  index:any
  counter(i: number) {
    this.index = i
    this.expandableData[i].show = !this.expandableData[i].show
  }
}
