import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { SwireTwoWayToggleButtonComponent } from '../swire-two-way-toggle-button/swire-two-way-toggle-button.component';

@Component({
  selector: 'swire-simple-toggle',
  templateUrl: './swire-simple-toggle.component.html',
  styleUrls: ['./swire-simple-toggle.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireSimpleToggleComponent
    }
  ]
})
export class SwireSimpleToggleComponent implements OnInit, ControlValueAccessor {
  @Input() swireDynamicClassName: string = '';
  @Input() items: any = [];
  disabled: boolean = false;
  szValue: boolean = false;
  touched: boolean = false;
  onChange = (szValue: any) => { };
  onTouched = () => { };

  constructor() { }

  writeValue(checked: boolean) {
    this.szValue = checked;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.checked;
      this.onChange(this.szValue);
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }
  onModelChange(e: any) {
    this.szValue = e;
    this.onChange(e);
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  ngOnInit(): void {
  }

}
