import { AfterViewInit, Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
@Component({
  selector: 'swire-tabs',
  templateUrl: './swire-tabs.component.html',
  styleUrls: ['./swire-tabs.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireTabsComponent
    }
  ]
})
export class SwireTabsComponent implements OnInit, ControlValueAccessor, AfterViewInit  {
  @ViewChild('mySwireTabUl') mySwireTabUl: any;
  @ViewChild('mySwireTabUlLi') mySwireTabUlLi: any;
  
  @Input() isPrimary:boolean = true;
  @Input() tabsDataList:any;
  @Input() isShowLeftArrow:boolean = false;
  @Input() isShowRightArrow:boolean = false;
  @Input() szRadioClass:string = 'default';
  @Input() swireDynamicDivWidth:string='350';
  ulWidth:string = '';
  liWidth:string = '';
  @Input() isDisabled: boolean = false;
  @Input() swiDynamicClassForTabs:string = '';

  
  activeClassName:string='';
  activeIndex = 0;
  
  constructor() { }

  ngOnInit(): void {
    console.log("tabsDataList>>>>>>>>>>>>.",this.tabsDataList);
  }
  ngAfterViewInit() {
    this.ulWidth = this.mySwireTabUl?.nativeElement?.offsetWidth;
    this.liWidth = this.mySwireTabUlLi?.nativeElement?.offsetWidth;
  }
  onChange = (szValue: any) => { };
  onTouch: any = () => { };
  touched = false;
  disabled = false;

  onTouched = () => { };
  
  writeValue(obj: any): void {
    this.activeIndex = obj;
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.onChange( this.activeIndex);
    }
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  updateActiveTab(index:number){
    this.activeIndex = index;
    this.onChangeHandler(this.activeIndex);
    if(this.szRadioClass == 'bg-grey-40'){
      this.szRadioClass = 'active';
    }else{
      this.szRadioClass = '';
    }
  }
  scroll(name:string){
    this.activeClassName = name
    if(name == 'left'){
      this.mySwireTabUl?.nativeElement?.scrollTo({ left: (this.mySwireTabUl?.nativeElement?.scrollLeft - this.mySwireTabUlLi?.nativeElement?.offsetWidth), behavior: 'smooth' });
    }else if(name == 'right'){
      this.mySwireTabUl?.nativeElement?.scrollTo({ left: (this.mySwireTabUl?.nativeElement?.scrollLeft + this.mySwireTabUlLi?.nativeElement?.offsetWidth), behavior: 'smooth' });
    }

  }

}
