import { Component, forwardRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-bread-crumbs',
  templateUrl: './swire-bread-crumbs.component.html',
  styleUrls: ['./swire-bread-crumbs.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwireBreadCrumbsComponent),
      multi: true
    }
  ]
})
export class SwireBreadCrumbsComponent implements OnInit, ControlValueAccessor {
  @Input() swireDynamicBreadCrumbsTexts: any = [];
  @Input() swireActiveBreadCrumbs: string = '';
  @Input() swireDynamicClassName: string = '';
  onChange: any;
  onTouch: any;
  disabled: boolean = false;
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  writeValue(val: any) {
  }
  onClick(name: any) {
    this.onChange(name);
  }
  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
  constructor() { }

  ngOnInit(): void {
  }

}
