import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
@Component({
  selector: 'swire-loader',
  templateUrl: './swire-loader.component.html',
  styleUrls: ['./swire-loader.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SwireLoaderComponent implements OnInit {
  addClass = true;
 @Input() swireLoader:any

  constructor() {
  }

 
  ngOnInit() {
    this.intervalFunction();
  }
  private intervalFunction() {
    this.addClass = true;
    setTimeout(() => {
      this.addClass = false;
      setTimeout(() => {
        this.intervalFunction();
      }, 0);
    }, 3000);
  }

}
