import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { AppConstants } from '../../../common/constant'

@Component({
  selector: 'swire-chips',
  templateUrl: './chips.component.html',
  styleUrls: ['./chips.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: ChipsComponent
    }
  ]
})
export class ChipsComponent implements OnInit, ControlValueAccessor {
  @ViewChild('inputField') inputField: any;
  @ViewChild('myInput') myInput: any;
  @Input() swireDynamicClassName: string = '';
  @Input() swireInputIconVisible: boolean = false;
  @Input() SwireMinChipsFilledValue: number = 0;
  @Input() SwireMaxChipsFilledValue: number = 0;
  showFocusClass: boolean = false;
  savedItem: any = [];
  szValue = null;
  touched = false;

  disabled = false;

  onChange = (szValue: any) => { };

  onTouched = () => { };

  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
  onKeyDown(e: any) {

  }
  onKeyPress(e: any) {

  }
  constructor() {

  }

  ngOnInit(): void {
  }
  divClicked() {
    this.inputField?.nativeElement?.focus();
  }
  btnPressed(e: any) {
    this.showFocusClass = false;
    if (this.SwireMaxChipsFilledValue > this.savedItem?.length) {
      if (e?.code == 'Enter' && e?.target?.value !== '') {
        let y = AppConstants?.removeSpaces(e?.target?.value);
        let x = y?.trim();
        this.savedItem.push(e?.target?.value);
        this.inputField.nativeElement.value = '';
        this.szValue = this.savedItem;
        this.onChange(this.szValue);
      } else if (e?.type == 'focusout' && e?.target?.value !== '') {
        let y = AppConstants?.removeSpaces(e?.target?.value);
        let x = y?.trim();
        this.savedItem.push(e?.target?.value);
        this.inputField.nativeElement.value = '';
        this.szValue = this.savedItem;
        this.onChange(this.szValue);        
      }
      else if (e?.code == 'Backspace' && e?.target?.value == '') {
        this.savedItem.pop();
        this.inputField.nativeElement.value = '';
        this.szValue = this.savedItem;
        this.onChange(this.szValue);
      }
    }
  }
  deleteData(name: string) {
    let index = this.savedItem.findIndex((elm: string) => elm == name);
    this.savedItem.splice(index, 1);
    this.szValue = this.savedItem;
    this.onChange(this.szValue);
  }
  focusIn(e: any) {
    this.showFocusClass = true;
  }
}
