import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'swire-accordian',
  templateUrl: './swire-accordian.component.html',
  styleUrls: ['./swire-accordian.component.scss']
})
export class SwireAccordianComponent implements OnInit {
  @Input() defaultExpand: boolean = false;
  @Input() swireExpandCollapseHeading: string = '';
  @Input() swireExpandCollapsePara1: string = '';
  @Input() swireExpandCollapsePara2: string = '';
  @Input() swireExpandCollapseHoverClass: string = '';
  @Input() swireExpandCollapseDynamicClass: string = '';
  @Input() swireDynamicHoverClass: string = '';

  constructor() { }

  ngOnInit(): void {
  }
  expandCollapse() {
    this.defaultExpand = !(this.defaultExpand);
  }

}
