import { Component, Input,ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'swire-range-datepicker',
  templateUrl: './swire-range-datepicker.component.html',
  styleUrls: ['./swire-range-datepicker.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: SwireRangeDatepickerComponent,
      multi: true
    }
  ]
})
export class SwireRangeDatepickerComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myDatePicker') myDatePicker: ElementRef | undefined;
  @Input() swireDynamicDatePlaceholder: string = '';
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicIconClass: string = '';
  @Input() swireDynamicColorClass: string = '';
  @Input() swireDynamicSizeClass: string = '';
  @Input() label: string = '';
  @Input() swireDynamicMinDate: any;
  @Input() swireDynamicMaxDate: any;
  @Input() swireDynamicStartDate: any;
  @Input() isDisabled: boolean=false;
  @Input() markDisabled:any;
 @Input () firstDayOfWeek:any
 @Input () range:any = ''

  @Input() showHideCal: boolean = false;
  hoveredDate!: NgbDate;
  disabled: boolean = false;
  fromDate!: NgbDate;
  toDate!: NgbDate;
  nullVal: any = null;
  onChange: any = () => { };
  onTouch: any = () => { };
  writeValue(obj: any): void {
    if (obj) {
      this.range = this.formatRange(obj.fromDate, obj.toDate)

    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
  dateValueChanged(): void {
    this.onChange(this.range)
  }
  constructor(public calendar: NgbCalendar) {
    // this.fromDate = calendar.getToday();
    // this.toDate = calendar.getNext(calendar.getToday(), 'd', 10);
  }

  ngOnInit(): void {
  }
  onDateSelection(date: NgbDate) {
    // let date: NgbDate = d;
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
      // drop.close()
      this.showHideCal = false
    } else {
      this.toDate = this.nullVal;
      this.fromDate = date;
    }
    console.log('date-->',this.fromDate, this.toDate)
    this.range = this.formatRange(this.fromDate, this.toDate)
    this.onChange(this.range)
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || date.equals(this.toDate) || this.isInside(date) || this.isHovered(date);
  }
  formatRange(fromDate: NgbDate, toDate: NgbDate) {
    if ((fromDate) && (toDate)) {
      return (fromDate ? '' + fromDate.year + "-" + ('00' + fromDate.month).slice(-2) + "-" + ('00' + fromDate.day).slice(-2) : "")
        + " to " +
        (toDate ? '' + toDate.year + "-" + ('00' + toDate.month).slice(-2) + "-" + ('00' + toDate.day).slice(-2) : "")
    }else{
      return '';
    }
  }
  showHideDate() {
    if (!this.disabled) {
      this.showHideCal = !(this.showHideCal);
    }
  }
  // markDisabled(date: NgbDate) {
  //   // const d = new Date(date.year, date.month - 1, date.day);
  //   // console.log('dte',date)
  //   return true
  // }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.onChange(this.range)
    }
  }
  onTouched = () => { };
  touched = false;
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }
  callFromParent(){
    this.showHideCal = false
    this.range = ''
    this.toDate = this.nullVal;
    this.fromDate = this.nullVal;
  }
  removeRange(){
    this.range = ''
    this.toDate = this.nullVal;
    this.fromDate = this.nullVal;
  }
  selectCurrentDate(){
    this.fromDate = this.calendar.getToday()
    this.toDate = this.calendar.getNext(this.calendar.getToday(), 'd', 20);
    this.range = this.formatRange(this.fromDate , this.toDate)
  }
}
