import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'swire-notifications',
  templateUrl: './swire-notifications.component.html',
  styleUrls: ['./swire-notifications.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SwireNotificationsComponent implements OnInit {
  @Input() type:string ='';
  @Input() text:string ='';
  @Input() subText:string ='';
  @Input() notificationType:string = '';
  @Input() isHideNotification:boolean = false;
  
  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(){
    this.isHideNotification = false;
  }

  closeNotifications(){
    this.isHideNotification = true;
  }

}
