import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'swire-prefix-dropdown',
  templateUrl: './swire-prefix-dropdown.component.html',
  styleUrls: ['./swire-prefix-dropdown.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwirePrefixDropdownComponent
    }
  ],
  host: {
    "(document:click)": "onClick($event)",
  },
})
export class SwirePrefixDropdownComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myInput') myInput: any;
  @ViewChild('currencyDiv') currencyDiv: ElementRef | undefined;
  @ViewChild('dataList') dataList: ElementRef | undefined;
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicCurrencyFilterData: any = [];
  @Input() swireDynamicFilterByName: any = '';
  @Input() swireDynamicDropDownOrText: boolean = false;
  @Input() swireDynamicIsPrefixDropDown: boolean = false;
  @Input() selectedCurrencyName:string= 'USD';
  showFocus: boolean = false;
  public filterdata: any = '';
  showCurrencyData: boolean = false;
  priceValue: any = null;
  @Input() disableSwireCurrencyDropdown:boolean = false;
  @Input() disableSwireCurrencyInput:boolean= false;


  szValue: any = null;
  touched = false;
  disabled: boolean = false;

  onChange = (szValue: any) => { };

  onTouched = () => { };

  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      let myData = {
        value: e.target.value,
        preFixValue: this.szValue?.preFixValue
      };
      this.szValue = myData;
      this.onChange(this.szValue);
    }
  }
  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }
  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }



  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onKeyDown(e: any) {

  }
  onKeyPress(e: any) {

  }


  constructor(private _eref: ElementRef) { }

  ngOnInit(): void {
  }
  selectedVal(val: any) {
    let myData = {
      value: this.priceValue,
      preFixValue: val[this.swireDynamicFilterByName]
    };
    this.szValue = myData;
    this.onChange(this.szValue);
    this.showCurrencyData = false;
    this.filterdata = this.szValue?.preFixValue;

  }
  showHideDropdaownData() {
    // this.filterdata = null;
    this.showCurrencyData = !(this.showCurrencyData);
    if (!this.showCurrencyData) {
      this.filterdata = this.szValue?.preFixValue;
    }
  }
  focusIn() {
    this.showFocus = true;
    this.filterdata = null;

  }
  focusOut() {
    this.showFocus = false;
  }
  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      this.showCurrencyData = false;
      this.filterdata = this.szValue?.preFixValue;
    }

  }

}
