import { Component, forwardRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-button',
  templateUrl: './swire-button.component.html',
  styleUrls: ['./swire-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SwireButtonComponent),
      multi: true
    }
  ]
})
export class SwireButtonComponent implements OnInit, ControlValueAccessor {
  @Input() dynamicBtnEnableDisable: boolean = false;
  @Input() dynamicBtnName: string = '';
  @Input() dynamicDirectionForIcon: string = '';
  @Input() dynamicShowBtnIcon: boolean = false;
  @Input() swireDynamicIconClass: string = '';
  @Input() swireDynamicColorClass: string = '';
  @Input() swireDynamicSizeClass: string = '';
  @Input() dynamicBtnDirectionClassName:string = '';

  
  onChange: any;
  onTouch: any;
  disabled: boolean = false;
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  writeValue(val: any) {
  }
  onClick() {
    this.onChange(true);
  }
  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
  @Input() dynamicBtnClassName: any = '';

  constructor() { }

  ngOnInit(): void {
  }

}
