import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-input',
  templateUrl: './swire-input.component.html',
  styleUrls: ['./swire-input.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireInputComponent
    }
  ]
})
export class SwireInputComponent implements ControlValueAccessor {
  @ViewChild('myInput') myInput:any;

  @Input() swireDynamicClassName: string = '';
  @Input() swireInputIconVisible: boolean = false;
  @Input() showShipIcon:boolean = false;
  @Input() showSearchIcon:boolean = false;

  szValue = null;
  touched = false;

  disabled = false;

  onChange = (szValue: any) => { };

  onTouched = () => { };

  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onKeyDown(e: any) {

  }
  onKeyPress(e: any) {

  }
  enter(e: any) {

  }

   

}
