import { Component, Input, OnChanges, OnInit, ViewEncapsulation, } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { SwireInputComponent } from '../../../swire-input/swire-input.component';
@Component({
  selector: 'swire-Indeterminate-checkbox',
  templateUrl: './swire-Indeterminate-checkbox.component.html',
  styleUrls: ['./swire-Indeterminate-checkbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireIndeterminateCheckboxComponent
      // useExisting: forwardRef(() => SwireIndeterminateCheckboxComponent),
    }
  ]

})

export class SwireIndeterminateCheckboxComponent implements OnInit, ControlValueAccessor, OnChanges {
  disabled: any;
  checked: any = null;
  @Input() isDisabled: boolean = false;
  @Input() indeterminateCheckBoxLabel: string = '';
  @Input() ischecked: any = null;
  @Input() name: string = '';
  @Input() swireDynamicClassName: string = '';
  @Input() swidynamicClassForIndeterminateState:string = '';
  @Input() countLength = [];
  @Input() showCount:boolean=true
  szValue = null;
  touched: Boolean = false;
  onChange: any = () => { };
  onTouch: any = () => { };
  writeValue(checked: boolean) {
    this.checked = checked;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouch = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouch();
      this.touched = true;
    }
  }
  setDisabledState(isDisabled: boolean) {
    this.isDisabled = isDisabled;

  }
  onModelChange(e: boolean) {
    // Step 5a: bind the changes to the local value
    this.checked = e;
    // Step 5b: Handle what should happen on the outside, if something changes on the inside
    this.onChange(e);
  }


  constructor() { }


  ngOnInit(): void {
  }
  ngOnChanges(): void {
  }


}
