import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'swire-stepper',
  templateUrl: './swire-stepper.component.html',
  styleUrls: ['./swire-stepper.component.scss'],  
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireStepperComponent
    }
  ]
})
export class SwireStepperComponent implements OnInit, ControlValueAccessor {
  @ViewChild('myInput') myInput: any;
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicMinVal: number = 0;
  @Input() swireDynamicMaxVal: number = Number.MAX_SAFE_INTEGER;

  szValue:any = null;
  touched = false;

  disabled:boolean = false;

  onChange = (szValue: any) => { };

  onTouched = () => { };

  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }

  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onKeyDown(e: any) {

  }
  onKeyPress(e: any) {

  }
  enter(e: any) {

  }


  constructor() { }

  ngOnInit(): void {
  }

  btnClicked(dir:string){
    if(dir == 'up'){
      this.szValue++;
      this.onChange(this.szValue);

    }else{
      this.szValue--;
      this.onChange(this.szValue);
    }

  }
}

