import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'swire-checkbox-filter',
  templateUrl: './swire-checkbox-filter.component.html',
  styleUrls: ['./swire-checkbox-filter.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireCheckboxFilterComponent
    }
  ],
  host: {
    '(document:click)': 'onClick($event)',
  },
})
export class SwireCheckboxFilterComponent implements OnInit, ControlValueAccessor {
  @ViewChild('toggleBtn') toggleButton: ElementRef | undefined;
  @ViewChild('filterList') listFilter: ElementRef | undefined;
  @ViewChild('myInput') myInput: any;
  @Input() filterCheckBoxData: any = [];
  @Input() atleastOneSelected !: boolean;
  @Input() btnName !: string;
  @Input() btnActiveClassName !: string;
  @Input() btnClassName !: string;
  @Input() bindLabel: string = '';
  @Input() bindValue !: string;
  @Input() swireDynamicClassName: string = '';
  @Input() showSearchBox:boolean = false;
  @Input() buttonClassForColor:string = '';
  @Input() searchBoxPlaceHolder:string = '';
  @Input() dropDownType:string = '';
  @Input() swireDynamicId:string = '';
  @Input() swireDynamicReferenceName:string = '';
  @Input() filterdatas: any = null;
  @ViewChild('inputDropdownFilter')
  inputDropdownFilter!: ElementRef;
  isTrue: boolean = true;
  public filterdata: any = null;
  public filterDataCheckbox: any = null;

  allCheckedItems: any = [];
  showList: boolean = false;
  @Input() showIndeterminateState:boolean = false;
  disabled: any;
  szValue: any = null;
  touched: any = null;
  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
    if (szValue) {
      this.onChangeHandler(szValue);
    }
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e?.target?.value;
      if (this.allCheckedItems.includes(e[this.bindLabel])) {
        const index = this.allCheckedItems.findIndex((elm: any) => elm == e[this.bindValue]);
        if (index >= 0) {
          this.allCheckedItems.splice(index, 1);
        }
      } else {
        this.allCheckedItems.push(e[this.bindLabel]);
      }
      if(this.allCheckedItems?.length > 0){
        this.showIndeterminateState = true;
      }else{
        this.showIndeterminateState = false;
      }
      this.szValue = this.allCheckedItems;
      this.onChange(this.allCheckedItems);
    }
  }


  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  constructor(
    private _fb: FormBuilder,
    private _eref: ElementRef
  ) { }

  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      this.showList = false;
    }

  }
  ngOnInit(): void {
    // this.onloadForm();
    // this.addCheckboxesToForm();
  }
  onSelected(item: any) {
    // this.markAsTouched();
    // this.allCheckedItems.length = 0;
    // this.filterCheckBoxData = this.filterCheckBoxData.map((elm: any) => {
    //   if (!item) {
    //     elm.checked = true;
    //   } else {
    //     if (elm?.name == item?.name) {
    //       elm.checked = elm?.checked == true ? false : true;
    //     }
    //   }
    //   if (elm.checked) {
    //     this.allCheckedItems.push(elm);
    //   }
    //   return elm;
    // });
    // this.szValue = this.allCheckedItems;
    // this.onChange(this.allCheckedItems);
    this.markAsTouched();
    this.filterCheckBoxData = this.filterCheckBoxData.map((elm: any) => {
      if (elm[this.bindValue] == item[this.bindValue]) {
        elm.checked = true;
        this.filterdata = [];
        this.filterDataCheckbox = [];
        this.szValue = elm[this.bindValue];       
      } else {
        elm.checked = false;
      }
      return elm;
    });
    this.onChange(this.szValue);
    this.showList = false;
  }
  focusIn() {
    this.showList = true;
  }
  showHideItems() {
    this.showList = !(this.showList);
  }
  selectAll(e: any) {
    if (e.target.checked && !this.showIndeterminateState) {
      this.allCheckedItems.length = 0;
      this.filterCheckBoxData.map((elm: any) => this.allCheckedItems.push(elm[this.bindLabel]));
    } else {
      this.allCheckedItems.length = 0;
      e.target.checked = false
    }
    this.showIndeterminateState = false;
    this.szValue = this.allCheckedItems;
    this.onChange(this.allCheckedItems);

  }
  checkExist(val: any) {
    if (this.szValue?.includes(val[this.bindValue])) {
      return true;
    } else {
      return false;
    }
  }
  checkAllExist() {
    if (this.szValue?.length == this.filterCheckBoxData?.length) {
      this.showIndeterminateState = false;
      return true;
    }    
    return false;

  }
}
