import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
@Component({
  selector: 'swire-dropdown',
  templateUrl: './swire-dropdown.component.html',
  styleUrls: ['./swire-dropdown.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireDropdownComponent
    }
  ],
  host: {
    "(document:click)": "onClick($event)",
  },
})
export class SwireDropdownComponent implements OnInit, ControlValueAccessor {
  @ViewChild('listDropdownFilter') listFilter: ElementRef | undefined;
  @ViewChild('myInput') myInput: any;
  @Input() filterDatas: any = [];
  @Input() atleastOneSelected: boolean = false;
  @Input() btnActiveClassName: string = '';
  @Input() btnClassName: string = '';
  @Input() bindLabel: string = '';
  @Input() bindValue: any = null;
  @Input() swireDynamicClassName: string = '';
  @Input() swireDynamicReferenceName: string = '';
  @Input() swireDynamicId: string = '';
  @Input() searchBoxPlaceHolder: string = '';
  @Input() dropDownType: string = '';
  @Input() showSearchBox: boolean = false;
  showIndeterminateState: boolean = false;
  public filterdata: any = null;
  public filterDataCheckbox: any = null;
  allCheckedItems: any = [];
  showList: boolean = false;
  disabled: boolean = false;
  szValue: any = null;
  touched: any = null;

  @ViewChild('inputDropdownFilter')
  inputDropdownFilter!: ElementRef;
  @ViewChild('listDropdownFilter')
  listDropdownFilter!: ElementRef;

  constructor(private _eref: ElementRef) { }

  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      this.showList = false;
    }
  }
  ngOnInit(): void {
  }


  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e?.target?.value;
      if (this.allCheckedItems.includes(e[this.bindLabel])) {
        const index = this.allCheckedItems.findIndex((elm: any) => elm == e[this.bindValue]);
        if (index >= 0) {
          this.allCheckedItems.splice(index, 1);
        }
      } else {
        this.allCheckedItems.push(e[this.bindLabel]);
      }
      if (this.allCheckedItems?.length > 0) {
        this.showIndeterminateState = true;
      } else {
        this.showIndeterminateState = false;
      }
      this.szValue = this.allCheckedItems;
      this.onChange(this.allCheckedItems);
    }
  }

  checkExist(val: any) {
    if (this.szValue?.includes(val[this.bindValue])) {
      return true;
    } else {
      return false;
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  selectAll(e: any) {
    if (e.target.checked) {
      this.allCheckedItems.length = 0;
      this.filterDatas.map((elm: any) => this.allCheckedItems.push(elm[this.bindLabel]));
    } else {
      this.allCheckedItems.length = 0;
    }
    this.showIndeterminateState = false;
    this.szValue = this.allCheckedItems;
    this.onChange(this.allCheckedItems);
  }

  checkAllExist() {
    if (this.szValue?.length == this.filterDatas?.length) {
      this.showIndeterminateState = false;
      this.filterdata = this.filterDataCheckbox;
      return true;
    }
    return false;
  }


  onSelected(item: any) {
    this.markAsTouched();
    this.filterDatas = this.filterDatas.map((elm: any) => {
      if (elm[this.bindValue] == item[this.bindValue]) {
        elm.checked = true;
        this.filterdata = [];
        this.filterDataCheckbox = [];
        this.szValue = elm[this.bindValue];       
      } else {
        elm.checked = false;
      }
      return elm;
    });
    this.onChange(this.szValue);
    this.showList = false;
  }
  showHideItems() {
    this.showList = !(this.showList);
  }
}
