import { Component, Input, OnInit, ViewEncapsulation, } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-radio-button',
  templateUrl: './swire-radio-button.component.html',
  styleUrls: ['./swire-radio-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireRadioButtonComponent
    }
  ]

})
export class SwireRadioButtonComponent implements OnInit, ControlValueAccessor {
  @Input() items: any = null;
  @Input() swireDynamicClassName: string = '';
  disabled: any;
  szValue: any = null;
  touched: any = null;
  onChange = (szValue: any) => {
    
   };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }
  selected() {

  }
  unselected() {
    
  }
  onModelChange(e: any) {
    this.szValue = e;
    this.onChange(e);
  }

  constructor() { }

  ngOnInit(): void {
  }

}
