import { Component, ElementRef, HostListener, Input, OnInit, Renderer2, ViewChild, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-filter-selectbox',
  templateUrl: './swire-filter-selectbox.component.html',
  styleUrls: ['./swire-filter-selectbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireFilterSelectboxComponent
    }
  ],
  host: {
    "(document:click)": "onClick($event)",
  },
})
export class SwireFilterSelectboxComponent implements OnInit, ControlValueAccessor {
  @Input() filterData: any = [];
  @Input() atleastOneSelected: boolean = false;
  @Input() btnName: string = '';
  @Input() btnActiveClassName: string = '';
  @Input() btnClassName: string = '';
  @Input() bindLabel: string = '';
  @Input() bindValue: any = null;
  @Input() swireDynamicClassName:string = '';
  @Input() searchBoxPlaceHolder:string = '';
  public filterdata: any = null;
  allCheckedItems: any = [];
  showList: boolean = false;
  disabled: boolean = false;
  szValue: any = null;
  touched: any = null;

  @ViewChild('toggleButtonFilter')
  toggleButton!: ElementRef;
  @ViewChild('listFilter')
  listFilter!: ElementRef;

  constructor(private _eref: ElementRef) { }

  onClick(event:any) {
    if (!this._eref.nativeElement.contains(event.target))
      this.showList = false;
  }
  ngOnInit(): void {
  }


  onChange = (szValue:any) => { };
  onTouched = () => { };
  writeValue(szValue:any) {
    this.szValue = szValue;
  }
  onChangeHandler(e:any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onSelected(item:any) {
    this.markAsTouched();
    this.allCheckedItems.length = 0;
    this.filterData = this.filterData.map((elm: any) => {
      if (elm[this.bindValue] == item[this.bindValue]) {
        elm.checked = elm?.checked == true ? false : true;
      }
      if (elm.checked) {
        this.allCheckedItems.push(elm);
      }
      return elm;
    });
    this.szValue = this.allCheckedItems;
    this.onChange(this.allCheckedItems);

  }
  showHideItems() {
    this.showList = !(this.showList);
  }




}
