import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'swire-toggle-selector-button',
  templateUrl: './swire-toggle-selector-button.component.html',
  styleUrls: ['./swire-toggle-selector-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireToggleSelectorButtonComponent
    }
  ]
})
export class SwireToggleSelectorButtonComponent implements OnInit, ControlValueAccessor {

  @Input() swireDynamicClassName: string = '';
  @Input() value1: string = "Option 1";
  @Input() value2: string = "Option 2";
  szValue: any = null;
  disabled:boolean=false
  onChange = (szValue: any) => { };
  onTouched = () => { };

  constructor() { }

  writeValue(szValue: any) {
    this.szValue = szValue;
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }


  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }

  ngOnInit(): void {
  }
  toggleButtonClick(type: string) {
    this.szValue = type;
    this.onChange(this.szValue);
  }
}
