import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'swire-sort-filter',
  templateUrl: './swire-sort-filter.component.html',
  styleUrls: ['./swire-sort-filter.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: SwireSortFilterComponent
    }
  ],
  host: {
    "(document:click)": "onClick($event)",
  },
})
export class SwireSortFilterComponent implements OnInit, ControlValueAccessor {
  @Input() showSearchBox:boolean = false;
  @Input() filterData: any = [];
  @Input() atleastOneSelected: boolean = false;
  @Input() sortFilterBtnName: string = '';
  @Input() btnActiveClassName: string = '';
  @Input() btnClassName!: string;
  @Input() bindLabel: string = '';
  @Input() bindValue: string = '';
  @Input() swireDynamicClassName: string = '';
  @Input() showDropDownIcon: boolean = false;
  @Input() showSortByText:boolean = false;
  @Input() showSortByTextVal:string = '';
  @Input() deviceName:boolean=true;
  @Input() searchBoxPlaceHolder:string = '';
  public filterdata = null;
  showList = false;
  disabled: any;
  szValue = null;
  touched: boolean = false;

  @ViewChild('toggleButtonSortFilter') toggleButton: ElementRef | undefined;
  @ViewChild('sortlistFilter') sortlistFilter: ElementRef | undefined;
  @ViewChild('myInput') myInput: any;

  constructor(private _eref: ElementRef) { }

  onClick(event: any) {
    if (!this._eref.nativeElement.contains(event.target)) {
      this.showList = false;
    }

  }
  ngOnInit(): void {
  }


  onChange = (szValue: any) => { };
  onTouched = () => { };
  writeValue(szValue: any) {
    this.szValue = szValue;
  }
  onChangeHandler(e: any) {
    this.markAsTouched();
    if (!this.disabled) {
      this.szValue = e.target.value;
      this.onChange(this.szValue);
    }
  }

  registerOnChange(onChange: any) {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: any) {
    this.onTouched = onTouched;
  }
  markAsTouched() {
    if (!this.touched) {
      this.onTouched();
      this.touched = true;
    }
  }

  setDisabledState(disabled: boolean) {
    this.disabled = disabled;
  }


  onSelected(item: any) {
    this.markAsTouched();
    this.filterData = this.filterData.map((elm: any) => {
      if (this.szValue == elm[this.bindValue]) {
        this.szValue = null;
        return elm;
      }
      if (elm[this.bindValue] == item[this.bindValue]) {
        elm.checked = true;
        this.szValue = elm[this.bindValue];
      } else {
        elm.checked = false;
      }
      return elm;
    });
    this.onChange(this.szValue);
    this.showList = false;
  }
  showHideItems() {
    this.showList = !(this.showList);
  }




}


